@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}
.tot-box{
  width:30%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color:white ;
  border-radius: 20px;
}
.user-url{
  width:80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
/* .user-size{
  width:80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
} */
.user-url-data{
  width: 100%;
  margin-bottom: 8px;
}
/* .user-size-data{
  width: 100%;
  margin-bottom: 8px;
} */
.butt{
  width: 70%;
  display: flex;
  justify-content: center;
  justify-content: space-around;
}
button{
  padding: 10px 20px;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 10px;
  margin-bottom: 10px;
  /* transition: background-color 0.3s; */
}
input[type="text" ]
{
padding: 10px;
border-radius: 5px;
margin-bottom: 10px;
font-size: 16px;
font-weight: 600;
border: 2px solid #3498db;
}
label{
  color: #3498db;
  font-size: medium;
  padding: 10px;
  font-weight: 500;
}
.generate{
background-color: #362bd8;
}
.generate:disabled{
  background-color:gray;
  cursor: not-allowed;
  }
.download{
background-color: #27ae60;
}
.download:hover{
  background-color: #2a8d54;
}
h3{
  font-weight: 600;
  font-size: medium;
  margin-bottom: 10px;
}
img{
  height: 40%;
  box-shadow: 2.4px 2.4px rgb(92, 90, 90);
}

@media screen and (max-width: 450px) {
  .tot-box {
    width: 80%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 20px;
  }
  .butt{
    width: 90%;
    display: flex;
    justify-content: center;
    justify-content: space-around;
  }
  button{
    padding: 5px 5px;
    border: none;
    color: white;
    cursor: pointer;
  }
  input[type="text" ]
  {
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 5px;
  font-size: 10px;
  font-weight: 500;
  border: 2px solid #3498db;
  }
  label{
    color: #3498db;
    font-size: medium;
    padding: 8px;
    font-size:small;
    font-weight: 500;
  }
}
@media only screen and (min-width: 431px) and (max-width: 1021px){
  .tot-box {
    width: 50%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 20px;
  }
  #root{
    /* background-color: #27ae60; */
  }
  .butt{
    width: 100%;
    display: flex;
    justify-content: center;
    justify-content: space-around;
  }

}